import { gql } from 'react-apollo'

//
// @param Object({ extraFields: Array })
//
export default ({ extraFields = [] }) => gql`
query fetchFacebookBotActivistsStrategy(
  $search: Json!
  $first: Int
) {
  query: getFacebookBotActivistsStrategy(
    search: $search,
    first: $first
  ) {
    totalCount
    activists: nodes {
      fbContextRecipientId
      ${extraFields.join(' ')}
    }
  }
}
`



